import { useEffect, useState } from "react"
import Footer from "./Footer"
import Navbar from "./Navbar"
import "../css/home.css"
import axios from "axios"
import Loading from "./Loading"
import { useSelector } from "react-redux/es/hooks/useSelector"
import loadingEvent from "../Redux/Action/loadingAction"
import { useDispatch } from "react-redux"

{/**29e8f9017a644297bce7980730911c63 */ }
const Home = () => {
    const loadval = useSelector(state => state)
    const dispatch=useDispatch()

    const [newsData, setnewsData] = useState([])
    const [mainnewsData, setMainnewsData] = useState([])
    const [fetchApiError,setError]=useState("")
    let path = window.location.pathname


    const current = new Date();
    const date = `${current.getFullYear()}-${current.getDate() - 1}-${current.getMonth() + 1}`
    const getDataApi=async ()=>
    {
        try
        {await fetch(`https://newsapi.org/v2/everything?q=tesla&from=${date}&sortBy=publishedAt&apiKey=29e8f9017a644297bce7980730911c63`)
        .then((res)=>{
            return  res.json()
        }).then((res)=>{
            setnewsData(res.articles)
            setMainnewsData(res.articles)
            dispatch(loadingEvent())
        })
        console.log("vibhav")
    }
    catch(error)
    {
        setError("Something Went Wrong")
        console.log(error.TypeEror)
        dispatch(loadingEvent())
    }

}
    



    useEffect(() => {


        getDataApi()
    
    }, [])

    const search_event = (e) => {
        e.preventDefault()

        const searchGetVal = document.querySelector("#search-input").value.toLowerCase()


        let searchVal = mainnewsData.filter((e) => {


            let val = e.title.toLowerCase().includes(searchGetVal)
            console.log(val)
            return val == true


        })


        if (searchVal.length == 0) {

            document.querySelector("#error").innerHTML = "Data Not Found"
            setnewsData(searchVal)


        }
        else {
            document.querySelector("#error").innerHTML = ""
            setnewsData(searchVal)


        }
    }
    return (<>
        <Navbar search_event={search_event}
            path={path} />


        <h1 id="latest-news-heading">Latest News</h1>
        {loadval ? <Loading/> : "" }
        <p id="error"></p>

        {   fetchApiError==="" ?
            newsData.map((e,i) => {
                return (
                    <div id="news-div">
                        <h2 key={i}><b style={{ color: "red" }}>Title:</b> {e.title}</h2>
                        <div id="img-div"> <img src={e.urlToImage} /></div>
                        <div id="auther-div">
                            <p><b>Auther:</b> {e.author}</p>
                            <p><b>Published:</b> {e.publishedAt}</p>
                            <p><b>Description: </b>{e.description}</p>
                            <p><b>Content: </b>{e.content} <a href={e.url} target="_black">Go For Full News</a></p>
                        </div>

                    </div>
                )
            })
            : <p class="apiError">{fetchApiError}</p>
        }







        <Footer />
    </>)
}

export default Home